.dialogs {
  display: flex;
  justify-content: center;
}
.dialogsItems {
  flex: 0 0 20%;
}
.messages {
  flex: 0 0 80%;
}
.message,
.messageRight {
  margin: 1rem 5rem;
}
.message p {
  margin-left: 1rem;
  word-break: break-all;
}
.messageRight {
  justify-content: flex-end;
}
.messageRight p {
  margin-right: 1rem;
}
.dialog a {
  color: #333;
}
.dialog {
  width: 75px;
  text-align: center;
  margin: 0 auto;
}
.img {
  max-width: 75px;
  width: 100%;
  height: 75px;
  overflow: hidden;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  background-color: #eee;
}
.mesBlock,
.mesBlockRight {
  display: flex;
  padding: 1.8rem;
  border-radius: 2.5rem;
  position: relative;
  -webkit-border-radius: 2.5rem;
  -moz-border-radius: 2.5rem;
  -ms-border-radius: 2.5rem;
  -o-border-radius: 2.5rem;
}
.mesBlock {
  box-shadow: 0 0 10px 0px #084093;
}
.mesBlockRight {
  box-shadow: 0 0 10px 0px #38b9f9;
}
.mesBlock::before,
.mesBlock::after,
.mesBlockRight::before,
.mesBlockRight::after {
  content: "";
  border: 0.7rem solid transparent;
  position: absolute;
}
.mesBlock::before,
.mesBlock::after {
  left: -1.2rem;
  border-right: 0.7rem solid;
}
.mesBlockRight::before,
.mesBlockRight::after {
  right: -1.25rem;
  border-left: 0.7rem solid;
}
.mesBlock::after {
  border-right: 0.7rem solid #ebfbfd;
}
.mesBlockRight::after {
  border-left: 0.7rem solid #ebfbfd;
}
.mesBlock p,
.mesBlockRight p {
  margin: 0 1rem;
  word-break: break-all;
}
.addMessage {
  width: 50%;
  margin: 0 auto;
}
.addMessage div {
  align-items: normal;
}
.addMessage textarea {
  width: 100%;
  display: flex;
  border-radius: 1rem 0 0 1rem;
  -webkit-border-radius: 1rem 0 0 1rem;
  -moz-border-radius: 1rem 0 0 1rem;
  -ms-border-radius: 1rem 0 0 1rem;
  -o-border-radius: 1rem 0 0 1rem;
}
.addMessage button {
  display: flex;
  align-self: normal;
  margin-left: 2rem;
  border-radius: 0 1rem 1rem 0;
}
